
#services .card p{
    color: white;
    font-weight: bold;
    text-transform: capitalize;
    /* transform: scale(0); */
    transition: all .3s ease-in-out;
    text-align: center;
    font-size: 0;
    
} 
#services .card:hover p{
   font-size: 12px;
    transform: scale(1);


}

@media screen and (min-width:760px) {
 
    #services .card:hover p{
        font-size: 16px;
         transform: scale(1);
     
     
     }
}




#services .card::before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.205);
    height: 100%;
    width: 100%;
    z-index: 1;
    

}
#services .card:hover::before{

    background: rgba(0, 0, 0, 0.678);
} 
